.header{
  text-align: center;
  margin: 30px 0px;
  line-height: 1.4em;
}

.bmcimg{
  width: 160px !important;
  box-shadow: 0 6px 12px #002fff55;
  border-radius: 10px;
  transition: box-shadow 0.3s, transform 0.3s;
}

.bmcimg:hover{
  box-shadow: 0 10px 30px #002fff55;
  transform: translateY(-4px);
}